.main-header {
  position: relative;
  height: auto;

  &-flex {
    display: flex;

    align-items: flex-end;
  }
  &-right {
    flex: 1;
    padding-left: 37px;
    &-text {
      margin-bottom: 20px;
      font-weight: 500;
      font-size: 22px;
    }
  }
  #main-header-avatar {
    width: 145px;
    height: 145px;

    border-radius: 10px;
    overflow: hidden;
    .spinner {
      zoom: 0.6;
    }
  }
  &-input {
    background: linear-gradient(
      74.31deg,
      #85ffc4 0%,
      #5cc6ff 45.83%,
      #bc85ff 80.73%
    );
    border-radius: 8px;

    display: flex;
    height: 50px;
    position: relative;
    input {
      padding-right: 20px;
      position: absolute;
      left: 2px;
      background: #1c1924;
      width: calc(100% - 250px);
      height: calc(100% - 4px);
      transform: translate(0, -50%);
      top: 50%;
      text-indent: 20px;
      border: none;
      border-radius: 8px;
      font-family: $main-font;
      letter-spacing: 0.15em;
      font-weight: 500;
      font-size: 16px;
    }
    #fetch-asset {
      position: absolute;
      right: 0px;
      width: 260px;
      height: 100%;
      background: linear-gradient(
        74.31deg,
        #85ffc4 0%,
        #5cc6ff 45.83%,
        #bc85ff 80.73%
      );
      border-radius: 8px;
      p {
        font-weight: bold;
        font-size: 16px;
        color: #1d0936;
      }
    }
  }
}

#connect {
  position: absolute;
  right: -280px;
  top: 20px;
  background: #01164a;
  border: 1px solid #bc85ff;
  border-radius: 18px;
  height: 50px;
  padding: 0px 26px;
  p {
    font-weight: 500;
    font-size: 16px;
  }
}

@media only screen and (max-width: 1640px) {
  #connect {
    right: 0px;
  }
}

@media only screen and (max-width: $mobile) {
  #connect {
    left: 50%;
    transform: translate(-50%);
    right: auto;
    top: 0px;
    p {
      white-space: nowrap;
    }
  }
  .main-header {
    &-flex {
      width: 100%;
      flex-direction: column;
      align-items: center;
      padding-top: 80px;
    }
    #main-header-avatar {
      width: 180px;
      height: 180px;
    }
    &-input {
      #fetch-asset {
        width: 90px;
      }
      input {
        width: calc(100% - 80px);
        text-indent: 10px;
      }
    }
    &-right {
      margin-top: 20px;
      padding-left: 0px;
      width: 100%;
      &-text {
        text-align: center;
        font-size: 20px;
      }
    }
  }
}
